
import Vue from 'vue';
import App from './App.vue';
// eslint-disable-next-line @typescript-eslint/no-var-requires
const props = require('../data/propertys.json');
export default Vue.extend({
    components: {
        App
    },
    mounted() {
        setTimeout(() => {

            this.$store.dispatch("WidgetMapStore/updateInmuebles",props);

        const configuracion = {
                mapaAltura:'90vh',
                showCluster:true                
            }

        this.$store.dispatch("WidgetMapStore/updateConfiguracionExterna",configuracion);
        },1000)
        
    }
    
})
